<template>
  <form action="" @submit.prevent="Save">
    <input-component
      label="Назва кінотеатра"
      id="cinemaName"
      type="text"
      placeholder="Введіть назву кінотеатра"
      v-model="cinemaName"
      :error="v$.cinemaName.$error ? 'Поле є обовязковим' : '' "
      />
      <textarea-component
      class="mt-3"
      label="Опис"
      id="cinemaDescription"
      placeholder="Введіть опис кінотеатра"
      v-model="cinemaDescription"
      :error="v$.cinemaDescription.$error ? 'Поле є обовязковим' : '' "
    />
    <textarea-component
      class="mt-3"
      label="Умови"
      id="cinemaConditions"
      placeholder="Введіть умови кінотеатра"
      v-model="cinemaConditions"
      :error="v$.cinemaConditions.$error ? 'Поле є обовязковим' : '' "
    />
    <img-component v-model="logoPicture" :class="{'is-invalid': v$.logoPicture.$error}" class="mt-3" @deleteMainPicture="delImage" title="Логотип"></img-component>
    <p v-if="v$.logoPicture.$error" class="error-message">Виберіть логотип</p>
    <img-component v-model="topBannerPicture" :class="{'is-invalid': v$.topBannerPicture.$error}" class="mt-3" @deleteMainPicture="delImage" title="Картинка верхнього банера"></img-component>
    <p v-if="v$.topBannerPicture.$error" class="error-message">Виберіть картинку верхнього банера</p>
    <imgs-component v-model="gallary" :class="{'is-invalid': v$.gallary.$error}" class="mt-3" @newImage="addImage" @delImage="delImage" @changeImage="changeImage"></imgs-component>
    <p v-if="v$.gallary.$error" class="error-message">Виберіть картинку</p>
    <input-component
      class="mt-3"
      label="Силка на трейлер"
      id="trailerLink"
      type="text"
      placeholder="Введіть силку на трейлер в youtube..."
      v-model="trailerLink"
      :error="v$.trailerLink.$error ? 'Поле є обовязковим' : '' "
    />
    <div class="form-group mt-3 ml-1">
      <p><strong>Тип фільма</strong></p>
      <div class="row" :class="{ 'is-invalid': v$.typeFilm.$error }">
        <checkbox-component v-model="typeFilm['3D']" id="3D" label="3D"></checkbox-component>
        <checkbox-component v-model="typeFilm['2D']" id="2D" label="2D"></checkbox-component>
        <checkbox-component
          v-model="typeFilm.IMAX"
          id="IMAX"
          label="IMAX"
        ></checkbox-component>
      </div>
      <p v-if="v$.typeFilm.$error" class="error-message">Виберіть тип фільма</p>
    </div>
    <seo-component v-model="SEO"></seo-component>
  </form>
</template>

<script>

import { useVuelidate } from '@vuelidate/core'
import { required } from '@vuelidate/validators'
import inputComponent from '@/components/UI/input-component.vue'
import textareaComponent from '@/components/UI/textarea-component.vue'
import imgComponent from '@/components/UI/img-component.vue'
import imgsComponent from '@/components/UI/imgs-component.vue'
import checkboxComponent from '@/components/UI/checkbox-component.vue'
import SeoComponent from '@/components/seo-component.vue'
import { CloudStorage } from '@/js/cloudStorage'
import { CloudFirestore } from '@/js/cloudFirestore'
export default {
  components: { inputComponent, imgComponent, imgsComponent, checkboxComponent, SeoComponent, textareaComponent },
  setup () {
    const v$ = useVuelidate()
    return { v$ }
  },
  data () {
    return {
      cinemaName: '',
      cinemaDescription: '',
      cinemaConditions: '',
      logoPicture: {},
      topBannerPicture: {},
      gallary: [],
      trailerLink: '',
      typeFilm: {
        '3D': false,
        '2D': false,
        IMAX: false
      },
      SEO: {
        link: '',
        title: '',
        keywords: '',
        description: ''
      },
      ListDeleteImages: [],
      cloudFirestore: new CloudFirestore(),
      cloudStorage: new CloudStorage()
    }
  },
  props: {
    language: String,
    collectionRef: Object,
    id: String,
    saveTriger: Boolean
  },
  mounted () {
    this.GetFilm()
  },
  validations () {
    return {
      cinemaName: { required, $lazy: true },
      cinemaDescription: { required, $lazy: true },
      cinemaConditions: { required, $lazy: true },
      logoPicture: { required, $lazy: true },
      topBannerPicture: { required, $lazy: true },
      gallary: { required, $lazy: true }
    }
  },
  computed: {
    fileFolderPath () {
      return `films/${this.language}/${this.id}`
    }
  },
  methods: {
    addImage (newImage) {
      this.gallary.push(newImage)
    },
    delImage (value) {
      this.ListDeleteImages.push(value)
    },
    changeImage (newImage) {
      this.gallary = this.gallary.map(image =>
        image.id === newImage.id ? newImage : image)
    },
    GetFilm () {
      this.ListDeleteImages = []
      this.cloudFirestore.getDocument(this.collectionRef, this.$route.params.id).then((document) => {
        if (document) {
          Object.assign(this.$data, document)
        }
      })
    },
    async Save () {
    }
  },
  watch: {
    saveTriger () {
      this.Save()
    }
  }
}
</script>

<style>
.error-message {
  color: red;
  font-size: 0.8em;
  margin-top: 0.2em;
}

.is-invalid {
  border: 1px solid red;
  border-radius: 5px;
}
</style>
